<template>
  <div>
    <filtros @filtros="get_vacante_data" />
    <!--  -->

    <div class=" flex justify-between">
      <div class="mt-3">
        <vs-button class=" focus:outline-none" @click="guardar_cambios()">
          Guardar Cambios
        </vs-button>
      </div>
    </div>
    <!--  -->

    <vs-table striped class=" mt-2">
      <template #header>
        <vs-checkbox success v-model="marcar_todos">
          <template #icon>
            <i class="fas fa-check-double"></i>
          </template>
          Marcar Todos
        </vs-checkbox>
      </template>

      <template #thead>
        <vs-tr>
          <vs-th
            v-for="(item, i) in columns"
            :key="i"
            sort
            @click="vacanteData = $vs.sortData($event, vacanteData, item.name)"
          >
            {{ item.text }}
          </vs-th>
        </vs-tr>
      </template>

      <template #tbody>
        <vs-tr :key="i" v-for="(tr, i) in vacanteData" :data="tr">
          <vs-td
            :edit="i2 == 0 || i2 == 1"
            @click="
              () => {
                if (i2 == 0 || i2 == 1) $emit('edit', tr);
              }
            "
            v-for="(item, i2) in columns"
            :key="i2"
          >
            <tr v-if="item.name === 'vacante'">
              <vs-checkbox success :val="tr" v-model="checksData[i]">
                <template #icon>
                  <i class="fas fa-check-double"></i>
                </template>
              </vs-checkbox>
            </tr>

            <tr v-else>
              {{
                tr[item.name]
              }}
            </tr>
          </vs-td>
        </vs-tr>
      </template>

      <template #notFound>
        No existen datos ðŸ˜¢
      </template>
    </vs-table>
  </div>
</template>

<script>
import filtros from "./filtros.vue";
import axios from "@//plugins//axios";

export default {
  components: { filtros },

  data() {
    return {
      filtros: {},
      columns: [
        {
          name: "vacante",
          text: "Estado",
        },

        {
          name: "escuela",
          text: "Escuela",
        },

        {
          name: "modalidad",
          text: "Modalidad",
        },

        {
          name: "sede",
          text: "Sede",
        },

        {
          name: "cant_vacante",
          text: "Vacantes",
        },
      ],
      vacanteData: [],
      checksData: [],
      marcar_todos: false,
    };
  },

  watch: {
    marcar_todos(newValue) {
      for (let index = 0; index < this.checksData.length; index++) {
        this.checksData[index] = newValue;
      }
    },
  },

  methods: {
    async get_vacante_data(e) {
      try {
        this.filtros = e;
        const escuela = this.filtros.escuela ? this.filtros.escuela.id : 0;
        const modalidad = this.filtros.modalidad
          ? this.filtros.modalidad.id
          : 0;
        const sede = this.filtros.sede ? this.filtros.sede.id : 0;

        const { data } = await axios.get(
          "get_vacante_by_modalidad_escuela_sede",
          {
            params: {
              escuela,
              modalidad,
              sede,
            },
          }
        );
        this.checksData = [];
        this.vacanteData = data;
        this.vacanteData.forEach((item, i) => {
          this.checksData[i] = item.vacante;
        });
      } catch (error) {
        return null;
      }
    },

    async guardar_cambios() {
      const loading = this.$vs.loading({
        color: "success",
      });

      try {
        if (this.vacanteData.length <= 0) {
          this.$vs.notification({
            title: "Vacante",
            text: "No existen datos en la fila! ðŸ˜¢",
            color: "danger",
          });
          return;
        }

        const final_data = this.vacanteData.map((item, i) => {
          return {
            id: item.id,
            vacante: this.checksData[i],
            cant_vacante: item.cant_vacante,
            modalidad: item.modalidad_id,
            escuela: item.escuela_id,
            sede: item.sede_id,
          };
        });

        const { status } = await axios.put("vacante/", final_data);

        if (status != 200) {
          this.$vs.notification({
            title: "Vacante",
            text: "Ocurrio un problema al guardar los datos! ðŸ˜¢",
            color: "danger",
          });
          return;
        }

        this.$vs.notification({
          title: "Vacante",
          text: "Los datos se guardaron correctamente! ðŸ˜Ž",
          color: "success",
        });
      } catch (error) {
        //
      } finally {
        loading.close();
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
