<template>
  <div class=" w-full">
    <div class=" flex flex-col">
      <span class=" text-lg font-medium">Filtro de Vacantes</span>
      <div class="w- full flex justify-between mt-3 items-center">
        <span class=" w-2/5">
          Escuela
          <v-select
            v-model="filtros.escuela"
            :options="get_escuela_select"
            label="nombre"
            index="id"
            class=" w-full"
          >
          </v-select>
        </span>

        <div class="mx-2"></div>

        <span class=" w-2/5">
          Modalidad
          <v-select
            v-model="filtros.modalidad"
            :options="get_modalidad_select"
            label="nombre"
            index="id"
            class=" w-full"
          >
          </v-select>
        </span>

        <div class="mx-2"></div>

        <span class=" w-1/5">
          Sede/Filial
          <v-select
            v-model="filtros.sede"
            :options="get_sede_select"
            label="nombre"
            index="id"
            class=" w-full"
          >
          </v-select>
        </span>

        <span>
          <vs-button
            class=" focus:outline-none"
            @click="$emit('filtros', filtros)"
          >
            Buscar
          </vs-button>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      filtros: { escuela: 0, modalidad: 0, sede: 0 },
    };
  },

  computed: {
    get_escuela_select() {
      return this.$store.getters["escuela/get_escuela_select"];
    },

    get_modalidad_select() {
      return this.$store.getters["modalidad/get_modalidad_select"];
    },

    get_sede_select() {
      return this.$store.getters["sede/get_sede_select"];
    },
  },

  mounted() {
    this.$store.dispatch("escuela/set_escuela");
    this.$store.dispatch("modalidad/set_modalidad");
    this.$store.dispatch("sede/set_sede");
  },
};
</script>

<style lang="scss" scoped></style>
